import androidx.compose.animation.core.animateDpAsState
import androidx.compose.animation.core.animateFloatAsState
import androidx.compose.animation.core.tween
import androidx.compose.animation.core.*
import androidx.compose.foundation.background
import androidx.compose.foundation.clickable
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.absoluteOffset
import androidx.compose.foundation.layout.offset
import androidx.compose.foundation.layout.size
import androidx.compose.runtime.Composable

import androidx.compose.runtime.*
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.rotate
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.DpOffset
import androidx.compose.ui.unit.dp
import kotlinx.coroutines.delay

val sandColor = Color(0xFFd9b95a)
@Composable
fun Sand(fallPositions: List<Pair<Dp,Dp>>, initialDelay: Long, centerOffset: Int, yOffset: Int = 0 ){
    var pos by remember { mutableStateOf(DpOffset(fallPositions.first().first,fallPositions.first().second)) }

    var positions by remember { mutableStateOf(fallPositions) }
    var rotationDegs = remember { mutableStateOf(0f) }


    fun updatePosition(p: Dp){
        if(positions.isNotEmpty()) {
            positions = positions.drop(1)
            val nextPos = if(positions.isNotEmpty()) positions.first() else pos.x to pos.y
            if(nextPos.first != pos.x){
                val delta = (nextPos.first - pos.x)
                if(delta > 0.dp)
                    rotationDegs.value += 90f
                else
                    rotationDegs.value -= 90f
            }
            pos = DpOffset(nextPos.first, nextPos.second)
        }
    }

    LaunchedEffect(true){
        delay(initialDelay)
        updatePosition(0.dp)
    }

    val animatedOffsetX by animateDpAsState(
       targetValue = pos.x,
        animationSpec = tween(
            durationMillis = 100,
            easing = LinearEasing
        )
    )


    val animatedOffsetY by animateDpAsState(
        targetValue = pos.y,
        animationSpec = tween(
            durationMillis = 150,
            easing = LinearEasing
        ),
        finishedListener = ::updatePosition
    )

    val animatedRotation by animateFloatAsState(
        targetValue = rotationDegs.value,
        animationSpec = tween(
            durationMillis = 100,
            easing = FastOutLinearInEasing
        )
    )


    Box(modifier = Modifier
        .offset(animatedOffsetX - centerOffset.dp, animatedOffsetY + yOffset.dp * 20)
        .rotate(animatedRotation)
        .size(20.dp,20.dp)
        .background(if(pos.y <= 20.dp) Color.Transparent else sandColor)
    ){}
}