import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import kotlinx.coroutines.delay

val backgroundColor = Color(0xFF0f0f23)


@Composable
fun Scene(walls: HashSet<Pair<Int,Int>>, fall: List<List<Pair<Dp, Dp>>>, xOffset: Int, yOffset: Int = 0, centerOffset: Int, floorLevel: Int = -1 ){
    val blockList by remember {
        mutableStateOf( walls.map{ it.first - xOffset to it.second} )
    }
   Box(modifier = Modifier.size(800.dp, 400.dp)
       .background(backgroundColor)
       .fillMaxSize(),
       contentAlignment = Alignment.TopCenter
   ){
       blockList.forEach {
          Block(it.first, it.second + yOffset, centerOffset)
       }
       if(floorLevel != -1){
           (0 .. (800/20)).forEach{ xpos ->
              Block(xpos, floorLevel + yOffset, 800/2)
           }
       }
       fall.forEachIndexed{idx, path ->
           Sand(path, idx * 100L, centerOffset, yOffset)

       }
   }
}

