import androidx.compose.foundation.layout.*
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp

@Composable
fun MainView() {

    val map = createBlocks(
        listOf("498,4 -> 498,6 -> 496,6",
            "503,4 -> 502,4 -> 502,9 -> 494,9"
        )

    )
    val xOffset = map.minOf{it.first}
    val yOffset = 2
    val xMax = map.maxOf{it.first}
    val yMax = map.maxOf{it.second}
    val centerOffset = (xMax - xOffset) * 20 / 2
    val mapClone = map.toHashSet()

    val floorLevel = yMax + 2
    val path = fallingSand(mapClone, floorLevel)

    Column(modifier = Modifier.fillMaxSize()) {
        Scene(map, path, xOffset, yOffset,centerOffset, floorLevel,)
    }
}