import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp

fun fallingSand(blockedMap: HashSet<Pair<Int,Int>>, floor: Int = -1): List<List<Pair<Dp, Dp>>>{

    val floorLevel = if(floor == -1) blockedMap.maxOf{ it.second } else floor

    val simFun =
        if(floor == -1) {
            ::simulateFalling
        }
        else :: simulateFallingFloor

    val paths: MutableList<List<Pair<Int,Int>>> = mutableListOf()

    val xOffset = blockedMap.minOf{it.first}
    while(true){
        val newSand = simFun(500 to 0, blockedMap, floorLevel)
        if(newSand.isEmpty())
            break
        if(newSand.last() == -1 to - 1) {
            paths.add(newSand)
            break
        }
        else {
            blockedMap.add(newSand.last())
            paths.add(newSand)
        }
    }

    return paths.map{path ->
        path.map {
            Pair(
                (it.first - xOffset).dp * 20,
                it.second.dp * 20
            )
        }
    }
}

fun simulateFallingFloor(startPos: Pair<Int,Int>, blockedMap: HashSet<Pair<Int,Int>>, floorLevel: Int): List<Pair<Int,Int>>{
    var currentPos = startPos
    val fallPath = mutableListOf<Pair<Int,Int>>()

    while(true){
        if(currentPos.second + 1 < floorLevel) {
            if (!blockedMap.contains(currentPos.first to currentPos.second + 1))
                currentPos = currentPos.first to currentPos.second + 1
            else if (!blockedMap.contains(currentPos.first - 1 to currentPos.second + 1))
                currentPos = currentPos.first - 1 to currentPos.second + 1
            else if (!blockedMap.contains(currentPos.first + 1 to currentPos.second + 1))
                currentPos = currentPos.first + 1 to currentPos.second + 1
            else //sand comes to rest
                break
        }
        else { //reached floor
            break
        }
        fallPath.add(currentPos)
    }

    return fallPath
}

fun simulateFalling(startPos: Pair<Int,Int>, blockedMap: HashSet<Pair<Int,Int>>, floorLevel: Int): List<Pair<Int,Int>>{
    var currentPos = startPos
    val fallPath = mutableListOf<Pair<Int,Int>>()

    while(true){
        if(!blockedMap.contains(currentPos.first to currentPos.second + 1))
            currentPos = currentPos.first to currentPos.second + 1
        else if(!blockedMap.contains(currentPos.first - 1 to currentPos.second + 1))
            currentPos = currentPos.first - 1 to currentPos.second + 1
        else if(!blockedMap.contains(currentPos.first + 1 to currentPos.second  + 1))
            currentPos = currentPos.first + 1 to currentPos.second + 1
        else
            break
        if(currentPos.second > floorLevel)
            break
        fallPath.add(currentPos)
    }

    if(currentPos.second > floorLevel)
        fallPath.add(-1 to -1)

    return fallPath

}