//val wallColor = Color(0xFFb4e1f9)
val wallColor = Color(0xFF646059)

@Composable
fun Block(x: Int, y: Int, centerOffset: Int){
    var pos by remember { mutableStateOf(DpOffset(x * 20.dp, y * 20.dp)) }
    Box(modifier = Modifier
        .offset(pos.x - centerOffset.dp, pos.y)
        .size(20.dp,20.dp)
        .background(wallColor)
    ){
    }
}